<template>
  <footer class="footer">
    <div class="container">
      <div class="footer__inner">
        <div class="footer__col">
          <router-link to="/" class="footer__logo">
            <svg width="160" height="26">
              <use xlink:href="#logo"></use>
            </svg>
          </router-link>
          <div class="footer__copyright">
            {{ copyright }}
          </div>
        </div>
        <div class="footer__col footer__col--links">
          <div class="footer__links">
            <a
              v-for="(link, l) in footerLinks"
              :key="l"
              class="footer__link"
              :href="link.href"
              @click.prevent="$root.goTo(link.href)"
            >
              {{ link.name }}
            </a>
          </div>
          <div class="footer__socials">
            <template v-for="(social, s) in socials">
              <a
                v-if="showSocial(social)"
                :key="s"
                class="footer__social"
                :href="social.href"
                target="_blank"
              >
                <svg :class="social.class" width="28" height="28" aria-hidden="true">
                  <use :xlink:href="social.icon"></use>
                </svg>
              </a>
            </template>
          </div>
        </div>
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  name: 'Footer',
  components: {},
  props: {},
  data() {
    return {
      copyright: 'OPYRIGHT  2022 ETHER DADDY. ALL RIGHTS RESERVED.'
    }
  },
  computed: {
    socials() {
      return this.$root.data.socials
    },
    footerLinks() {
      return this.$root.data.footerLinks
    },
    isPresaleStart() {
      return this.$root.isPresaleStart
    }
  },
  methods: {
    showSocial(social) {
      if (this.isPresaleStart) {
        return social.text !== 'discord'
      }
      return true
    }
  }
}
</script>
