<template>
  <header class="header" :class="{ 'header--fixed': forceWL }">
    <div class="container container--wide">
      <div class="header__inner">
        <router-link to="/" class="header__logo">
          <svg width="240" height="38">
            <use xlink:href="#logo"></use>
          </svg>
        </router-link>
        <ul class="header__nav">
          <li v-if="!forceWL" class="header__item">
            <a class="header__link" href="#" @click.prevent="$root.goTo('#moneymap')">
              Daddy's Moneymap
            </a>
          </li>
          <li v-if="!isPresaleEnd && !forceWL" class="header__item">
            <a
              class="header__link header__link--eligible"
              href="#"
              @click.prevent="$root.goTo('#congrats')"
            >
              Am I eligible for the pre-sale?
            </a>
          </li>
          <li v-if="isWalletConnected" class="header__item">
            <a class="header__link" href="https://opensea.io/account/therealdaddy" target="_blank">
              My Daddies
            </a>
          </li>
          <li class="header__item">
            <a
              :class="isWalletConnected ? '' : 'header__link--connect'"
              class="header__link"
              href="#"
              @click.prevent="connectMetaMask()"
            >
              {{ connectButtonText }}
            </a>

            <template v-if="isWalletConnected">
              <p
                class="header__wallet"
                :class="inWhitelist ? 'header__wallet--success' : 'header__wallet--fail'"
              >
                {{ shortWallet }}
              </p>
            </template>
          </li>
        </ul>
        <div class="header__socials">
          <template v-for="(social, s) in socials">
            <a
              v-if="showSocial(social)"
              :key="s"
              class="header__social"
              :href="social.href"
              target="_blank"
            >
              <svg :class="social.class" aria-hidden="true">
                <use :xlink:href="social.icon"></use>
              </svg>
            </a>
          </template>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: 'Header',
  components: {},
  props: {},
  data() {
    return {
      menuOpened: false
    }
  },
  computed: {
    isWalletConnected() {
      return this.$root.isWalletConnected
    },
    currentWallet() {
      return this.$root.currentWallet
    },
    inWhitelist() {
      return this.$root.inWhitelist
    },
    soldOut() {
      return this.$root.soldOut
    },
    isPresaleStart() {
      return this.$root.isPresaleStart
    },
    isPresaleEnd() {
      return this.$root.isPresaleEnd
    },
    isPresaleOn() {
      return this.$root.isPresaleOn
    },
    isSaleOn() {
      return this.$root.isSaleOn
    },
    forceWL() {
      return this.$root.forceWL
    },
    isMobile() {
      return this.$root.isMobile
    },
    connectButtonText() {
      return this.isWalletConnected
        ? 'CONNECTED'
        : this.isMobile && this.isWalletConnected
        ? 'CONNECT'
        : 'CONNECT WALLET'
    },
    socials() {
      return this.$root.data.socials
    },
    shortWallet() {
      const { currentWallet } = this
      return currentWallet.substr(0, 11) + '...' + currentWallet.substr(currentWallet.length - 3)
    }
  },
  mounted() {
    window.addEventListener('resize', this.menuClose)
    window.addEventListener('scroll', this.menuClose)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.menuClose)
    window.removeEventListener('scroll', this.menuClose)
  },
  created() {
    document.addEventListener('keyup', evt => {
      if (evt.key === 27) {
        this.menuClose()
      }
    })
  },
  methods: {
    showSocial(social) {
      if (this.isPresaleStart && !this.soldOut) {
        return social.text !== 'discord'
      }
      return true
    },
    connectMetaMask() {
      this.$root.connectMetaMask()
    },
    menuToggle() {
      this.menuOpened = !this.menuOpened
    },
    menuClose() {
      this.menuOpened = false
    }
  }
}
</script>
