<template>
  <div class="public">
    <div class="public__inner">
      <p class="public__title">Public sale starts in</p>
      <div class="public__counter">
        <div class="public__number">
          <div class="public__numbers">
            <span>{{ computedCountDown.days[0] }}</span>
            <span>{{ computedCountDown.days[1] }}</span>
          </div>
          <p class="public__detail">days</p>
        </div>
        <i>:</i>
        <div class="public__number">
          <div class="public__numbers">
            <span>{{ computedCountDown.hours[0] }}</span>
            <span>{{ computedCountDown.hours[1] }}</span>
          </div>
          <p class="public__detail">hours</p>
        </div>
        <i>:</i>
        <div class="public__number">
          <div class="public__numbers">
            <span>{{ computedCountDown.minutes[0] }}</span>
            <span>{{ computedCountDown.minutes[1] }}</span>
          </div>
          <p class="public__detail">minutes</p>
        </div>
        <i>:</i>
        <div class="public__number">
          <div class="public__numbers">
            <span>{{ computedCountDown.seconds[0] }}</span>
            <span>{{ computedCountDown.seconds[1] }}</span>
          </div>
          <p class="public__detail">seconds</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CountdownPublic',
  components: {},
  props: {},
  data() {
    return {}
  },
  computed: {
    computedCountDown() {
      const { days, hours, minutes, seconds } = this.$root.countDownSale
      return {
        days: this.$formatNumber(days),
        hours: this.$formatNumber(hours),
        minutes: this.$formatNumber(minutes),
        seconds: this.$formatNumber(seconds)
      }
    }
  },
  watch: {},
  mounted() {},
  methods: {}
}
</script>
