/* eslint-disable prettier-vue/prettier */
import Vue from 'vue'
import App from './App.vue'
import Notifications from 'vue-notification'
import router from '@/router'
import VueMeta from 'vue-meta'
import Bowser from 'bowser'
import Cookies from 'js-cookie'
import SlideUpDown from "vue-slide-up-down";
import axios from 'axios'
import Web3 from 'web3'
import smoothscroll from 'smoothscroll-polyfill';

import './assets/sass/app.scss'

import Helpers from './utils/helpers.js'
import data from '@/utils/data.js'
import {
  addressOpensea,
  abiOpensea,
  addressEther,
  abiEther
} from '@/utils/contracts.js'

const delimiter = 1e18

const infura = 'https://mainnet.infura.io/v3/4a91eb50e36d430681e0000756d6261b'

const requestMethods = {
  getAccountsList: 'eth_accounts',
  callWalletConnection: 'eth_requestAccounts',
  callSwitchWallet: 'wallet_requestPermissions',
}

const walletConnectionProcessStartedErrorCode = -32002
const walletConnectionHeaderErrorCode = -32000

Vue.component("slide-up-down", SlideUpDown);

// kick off the polyfill!
smoothscroll.polyfill();
Vue.use(Helpers)
Vue.use(Helpers)
Vue.use(Notifications)
Vue.use(VueMeta, {
  refreshOnceOnNavigation: true
})
Vue.config.productionTip = false

window.appVue = new Vue({
  data() {
    return {
      data: data,
      windowTop: 0,
      width: null,
      height: null,
      desctopMinBreakpoint: 1500,
      tabletBreakpoint: 1200,
      tabletSmallBreakpoint: 1024,
      mobileBreakpoint: 768,
      bowser: null,
      isMetaMaskInstalled: false,
      isWalletConnected: false,
      currentWallet: null,
      web3: new Web3(Web3.givenProvider || infura),
      contractOpensea: null,
      contractEther: null,
      amountOpensea: 0,
      countDownPresale: {
        preCountTime: new Date('2022-02-20T23:00:00.000+03:00').getTime(),
        startTime: new Date('2022-02-22T23:00:00.000+03:00').getTime(),
        endTime: new Date('2022-02-23T00:28:00.000+03:00').getTime(),
        days: null,
        hours: null,
        minutes: null,
        seconds: null
      },
      countDownSale: {
        startTime: new Date('2022-02-23T00:30:00.000+03:00').getTime(),
        endTime: new Date('2024-02-23T00:30:00.000+03:00').getTime(),
        days: null,
        hours: null,
        minutes: null,
        seconds: null
      },
      isPreCount: false,
      isPresaleStart: false,
      isPresaleEnd: false,
      isPresaleOn: false,
      isSaleOn: false,
      soldOut: false
    }
  },
  computed: {
    isDesctopMin() {
      return this.width <= this.desctopMinBreakpoint
    },
    isTablet() {
      return this.width <= this.tabletBreakpoint
    },
    isSmallTablet() {
      return this.width <= this.tabletSmallBreakpoint
    },
    isMobile() {
      return this.width <= this.mobileBreakpoint
    },
    isMobileDevice() {
      return this.isMobile || this.bowser.platform.type !== 'desktop'
    },
    amountWhitelist() {
      return this.isPreCount ? 1 : 1.5
    },
    inWhitelist() {
      return this.amountOpensea > this.amountWhitelist
    },
    forceWL() {
      return this.$route.name === 'WLSale'
    },
    domain() {
      return this.forceWL ? 'secretmint.etherdaddy.io' : 'etherdaddy.io'
    }
  },
  watch: {
    currentWallet(newV, oldV) {
      if (newV !== null) {
        this.getAmountOpensea()
      }
    }
  },
  async created() {
    this.width = window.innerWidth
    this.height = window.innerHeight
    this.bowser = Bowser.parse(window.navigator.userAgent)

    this.handleTimerCountDownPresale()
    this.handleTimerCountDownSale()
    setInterval(() => {
      this.handleTimerCountDownPresale()
      this.handleTimerCountDownSale()
    }, 1000)

    await this.checkNetwork()
    await this.initContracts()
    this.initMetamask()
  },
  mounted() {
    this.addListeners()
    setTimeout(() => {
      if (!this.goToAnchor()) {
        this.goToCongrats()
      }
    }, 500);
  },
  methods: {
    async initContracts() {
      debugger;
      this.contractOpensea = new this.web3.eth.Contract(abiOpensea, addressOpensea)
      this.contractEther = new this.web3.eth.Contract(abiEther, addressEther)
      console.log(this.contractOpensea)
      console.log(addressEther)
      return Promise.resolve()
    },
    initMetamask() {
      const ethereum = window.ethereum

      this.isMetaMaskInstalled = ethereum ? ethereum.isMetaMask : false

      console.log(window)

      if (this.isMetaMaskInstalled) {
        ethereum.on('accountsChanged', accounts => {
          this.handleAccountsChange(accounts)
        })
        this.ethereumRequest(requestMethods.getAccountsList)
      }
    },
    handleAccountsChange(accounts) {
      this.setWalletConnectionStatus(accounts)
    },
    connectMetaMask() {
      if (this.isWalletConnected) {
        this.$notify({
          group: 'app-notifications',
          type: 'success',
          text: 'Metamask is already connected'
        })

        this.switchWallet()
      } else {
        this.FBCompleteRegistration()

        if (this.isMetaMaskInstalled) {
          this.ethereumRequest(requestMethods.callWalletConnection)
        } else {
          this.istallMetamask()
        }
      }
    },
    istallMetamask() {
      if (this.isMobileDevice) {
        document.location.href = `https://metamask.app.link/dapp/${document.location.host}/`
      }

      this.$notify({
        group: 'app-notifications',
        type: 'error',
        title: 'MetaMask is not installed',
        text: this.isMobileDevice ? `<a href="https://metamask.app.link/dapp/${document.location.host}/">Download here</a>` : `<a href="https://metamask.io/download.html" target="_blank">Download here</a>`
      })
    },
    async setWalletConnectionStatus(accounts) {
      this.isWalletConnected = !!accounts.length
      if (this.isWalletConnected) {
        if (this.currentWallet) {}
        this.currentWallet = accounts[0].toLowerCase()

        this.FBContact()
      } else {
        this.currentWallet = null
      }
    },
    ethereumRequest(method) {
      const ethereum = window.ethereum

      ethereum.request({
          method
        })
        .then(response => {
          if (method === requestMethods.getAccountsList) {
            this.setWalletConnectionStatus(response)
          }
        })
        .catch(error => {
          if (error.code === walletConnectionProcessStartedErrorCode) {
            this.$notify({
              group: 'app-notifications',
              type: 'error',
              text: 'Wallet connection process already started. Please finish or cancel it.'
            })
          }
          if (error.code === walletConnectionHeaderErrorCode) {}
        })
    },
    switchWallet() {
      const ethereum = window.ethereum

      ethereum.request({
          method: requestMethods.callSwitchWallet,
          params: [{
            eth_accounts: {}
          }]
        })
        .then(() => {})
    },
    async checkNetwork() {
      const netId = await this.web3.eth.getChainId().catch(error => {
        console.log(error)
      })

      switch (netId) {
        case 1:
          console.log('This is mainnet')
          break
        case 2:
          console.log('This is the deprecated Morden test network.')
          break
        case 3:
          console.log('This is the ropsten test network.')
          break
        default:
          console.log('This is an unknown network.')
      }

      if (Number(netId) !== 1) {
        this.web3 = new Web3(infura)

        this.$notify({
          group: 'app-notifications',
          type: 'error',
          text: 'You are on the wrong network. Please switch to Ethereum Mainnet!'
        })
      }

      return Promise.resolve()
    },
    addListeners() {
      window.addEventListener('scroll', this.onScroll)
      window.addEventListener('resize', this.onResize)
      document.addEventListener('gesturestart', function (e) {
        e.preventDefault()
      })
    },
    onScroll(e) {
      this.windowTop = window.pageYOffset
    },
    onResize(state) {
      this.width = window.innerWidth
      this.height = window.innerHeight
      document.body.classList.remove('scroll-off')
    },
    async getAmountOpensea() {
      this.amountOpensea = Cookies.get(`amountOpensea_${this.currentWallet}`) || 0

      const eventsAll = await Promise.all([
        this.contractOpensea.getPastEvents('OrdersMatched', {
          filter: {
            maker: this.currentWallet
          },
          fromBlock: 0,
          toBlock: 'latest'
        }),
        this.contractOpensea.getPastEvents('OrdersMatched', {
          filter: {
            taker: this.currentWallet
          },
          fromBlock: 0,
          toBlock: 'latest'
        })
      ]).catch(error => {
        console.log(error)
      })

      const [eventsMaker, eventsTaker] = eventsAll

      const events = [...eventsMaker, ...eventsTaker];

      let amount = 0

      events.forEach(event => {
        amount += event.returnValues.price / delimiter
      });

      this.amountOpensea = amount

      Cookies.set(`amountOpensea_${this.currentWallet}`, amount)
    },
    async getTransactions() {
      const latestBlock = await this.web3.eth.getBlock('latest');

      const response = await axios.get(`https://api.etherscan.io/api?module=account&action=txlist&address=${this.currentWallet}&startblock=0&endblock=${latestBlock.number}&sort=desc&apikey=ISJRU82G5DAII9QVVVGVY7ERE7GDTFPJY2`).catch(error => {
        console.log(error)
        return error
      })

      console.log(response)

      if (parseInt(response.data.status) !== 1) {
        setTimeout(() => {
          this.getTransactions()
        }, 1500);
      }
    },
    handleTimerCountDownPresale() {
      const currentTime = new Date().getTime()
      const distancePreCount = this.countDownPresale.preCountTime - currentTime
      const distanceStart = this.countDownPresale.startTime - currentTime
      const distanceEnd = this.countDownPresale.endTime - currentTime

      this.isPreCount = distancePreCount <= 0
      this.isPresaleStart = distanceStart <= 0
      this.isPresaleEnd = distanceEnd <= 0
      this.isPresaleOn = distanceStart <= 0 && distanceEnd > 0

      this.countDownPresale.days = Math.floor(distanceStart / (1000 * 60 * 60 * 24))
      this.countDownPresale.hours = Math.floor(
        (distanceStart % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
      )
      this.countDownPresale.minutes = Math.floor((distanceStart % (1000 * 60 * 60)) / (1000 * 60))
      this.countDownPresale.seconds = Math.floor((distanceStart % (1000 * 60)) / 1000)
    },
    handleTimerCountDownSale() {
      const currentTime = new Date().getTime()
      const distanceStart = this.countDownSale.startTime - currentTime
      const distanceEnd = this.countDownSale.endTime - currentTime

      this.isSaleOn = distanceStart <= 0 && distanceEnd > 0

      this.countDownSale.days = Math.floor(distanceStart / (1000 * 60 * 60 * 24))
      this.countDownSale.hours = Math.floor(
        (distanceStart % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
      )
      this.countDownSale.minutes = Math.floor((distanceStart % (1000 * 60 * 60)) / (1000 * 60))
      this.countDownSale.seconds = Math.floor((distanceStart % (1000 * 60)) / 1000)
    },
    goToCongrats() {
      if (!this.isMobile && !this.isPresaleStart) {
        setTimeout(() => {
          if (this.windowTop < 1000) {
            this.$scrollTo('#congrats')
          }
        }, 6000)
      }
    },
    async goTo(link) {
      if (!this.$scrollTo(link)) {
        try {
          await this.$router.push({
            path: '/'
          })
        } catch (error) {}
        setTimeout(() => {
          this.$scrollTo(link)
        }, 500)
      }
    },
    goToAnchor() {
      const {
        hash
      } = document.location
      const offset = ['#moneymap', '#faq'].includes(hash) ? 120 : 0
      return this.$scrollTo(document.location.hash, offset)
    },
    FBCompleteRegistration() {
      try {
        /* eslint-disable-next-line */
        FbEvents.CompleteRegistration()
      } catch (error) {
        console.log(error)
      }
    },
    FBContact() {
      try {
        /* eslint-disable-next-line */
        FbEvents.Contact()
      } catch (error) {
        console.log(error)
      }
    }
  },
  router,
  render: h => h(App)
}).$mount('#app')
