<template>
  <div class="countdown">
    <div class="container">
      <div class="countdown__wrapper">
        <div class="countdown__inner">
          <div class="countdown__title">
            <p>Pre-sale starts in</p>
          </div>

          <div class="countdown__counter">
            <div class="countdown__number">
              <div class="countdown__numbers">
                <span>{{ computedCountDown.days[0] }}</span>
                <span>{{ computedCountDown.days[1] }}</span>
              </div>
              <div class="countdown__detail"><span>days</span></div>
            </div>
            <i>:</i>
            <div class="countdown__number">
              <div class="countdown__numbers">
                <span>{{ computedCountDown.hours[0] }}</span>
                <span>{{ computedCountDown.hours[1] }}</span>
              </div>
              <div class="countdown__detail"><span>hours</span></div>
            </div>
            <i>:</i>
            <div class="countdown__number">
              <div class="countdown__numbers">
                <span>{{ computedCountDown.minutes[0] }}</span>
                <span>{{ computedCountDown.minutes[1] }}</span>
              </div>
              <div class="countdown__detail"><span>minutes</span></div>
            </div>
            <i>:</i>
            <div class="countdown__number">
              <div class="countdown__numbers">
                <span>{{ computedCountDown.seconds[0] }}</span>
                <span>{{ computedCountDown.seconds[1] }}</span>
              </div>
              <div class="countdown__detail"><span>seconds</span></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Countdown',
  components: {},
  props: {},
  data() {
    return {}
  },
  computed: {
    computedCountDown() {
      const { days, hours, minutes, seconds } = this.$root.countDownPresale
      return {
        days: this.$formatNumber(days),
        hours: this.$formatNumber(hours),
        minutes: this.$formatNumber(minutes),
        seconds: this.$formatNumber(seconds)
      }
    }
  },
  watch: {},
  mounted() {},
  methods: {}
}
</script>
