<template>
  <section id="join" class="join">
    <div class="container">
      <div class="join__inner">
        <div class="join__texts">
          <h2>Join us</h2>

          <div class="join__socials">
            <template v-for="(social, s) in socials">
              <a
                v-if="showSocial(social)"
                :key="s"
                class="join__social"
                :href="social.href"
                target="_blank"
              >
                <svg :class="social.class" width="27" height="27" aria-hidden="true">
                  <use :xlink:href="social.icon"></use>
                </svg>
                <span v-html="social.text"></span>
              </a>
            </template>
          </div>
        </div>

        <video
          v-if="!$root.isMobile"
          class="join__video"
          poster="@/assets/img/umbrella.png"
          autoplay
          loop
          muted
        >
          <source src="@/assets/media/umbrella-changed.mp4" type="video/mp4" />
        </video>

        <div v-if="$root.isMobile" class="join__picture">
          <img :src="img.src" :alt="img.alt" />
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Join',
  data() {
    return {
      img: {
        src: require('@/assets/img/umbrella.png'),
        alt: 'Umbrella'
      }
    }
  },
  computed: {
    socials() {
      return this.$root.data.socials
    },
    isPresaleStart() {
      return this.$root.isPresaleStart
    }
  },
  methods: {
    showSocial(social) {
      if (this.isPresaleStart) {
        return social.text !== 'discord'
      }
      return true
    }
  }
}
</script>
