<template>
  <div class="presale__buttons">
    <button class="button button--minting-counter" :disabled="isDecreaseDisabled" @click="decrease">
      <svg width="17" height="28" aria-hidden="true">
        <use xlink:href="#arrow-white"></use>
      </svg>
    </button>
    <div class="presale__value">
      <input ref="input" v-model.number="computedValue" type="number" readonly />
    </div>
    <button class="button button--minting-counter" :disabled="isIncreaseDisabled" @click="increase">
      <svg width="17" height="28" aria-hidden="true">
        <use xlink:href="#arrow-white"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  name: 'PlusMinusInput',
  props: {
    currentValue: {
      type: Number,
      required: true
    },
    maxValue: {
      type: Number,
      required: true
    },
    minValue: {
      type: Number,
      required: true
    }
  },
  computed: {
    computedValue: {
      get() {
        return this.currentValue
      },
      set(val) {
        if (val >= this.minValue && val <= this.maxValue && Number.isInteger(val)) {
          this.$emit('update:currentValue', val)
        }
      }
    },
    isIncreaseDisabled() {
      return this.currentValue >= this.maxValue
    },
    isDecreaseDisabled() {
      return this.currentValue <= this.minValue
    }
  },
  mounted() {
    this.$refs.input.focus()
  },
  methods: {
    decrease() {
      if (this.currentValue > this.minValue) {
        this.$emit('update:currentValue', this.currentValue - 1)
      }
    },
    increase() {
      if (this.currentValue < this.maxValue) {
        this.$emit('update:currentValue', this.currentValue + 1)
      }
    }
  }
}
</script>
