<template>
  <transition name="fade">
    <div v-if="isMounted" class="presale" :class="{ 'presale--w80': isPresaleStart }">
      <div class="container">
        <div v-if="!end.state" class="presale__inner">
          <CountdownPublic v-if="isPresaleOn && !isWLSale" />
          <div v-else-if="isSaleOn && !forceWL" class="public"></div>

          <div class="presale__info">
            <h2 class="presale__title">{{ title }}</h2>
            <p class="presale__subtitle">{{ titleMax }}</p>

            <div class="presale__buttons-wrapper">
              <PlusMinusInput
                :current-value.sync="currentCount"
                :max-value="maxMintCount"
                :min-value="minMintCount"
              />
              <template>
                <button class="button button--mint-for" @click.prevent="handleMint">
                  {{ priceText }}
                </button>
              </template>
            </div>
            <transition name="fade">
              <div v-if="message.show" class="presale__message">
                <span>{{ message.title }}</span>
                <small>{{ message.text }}</small>
              </div>
            </transition>
          </div>

          <Minted v-if="!isWLSale" :minted="computedMintedCount" :total="computedTotalCount" />
        </div>

        <div v-else class="presale__inner">
          <div class="presale__info">
            <div class="presale__end" v-html="end.title"></div>
          </div>
        </div>
      </div>

      <transaction :title.sync="modal.title" :text.sync="modal.text" :show.sync="modal.show" />
    </div>
  </transition>
</template>

<script>
import Cookies from 'js-cookie'

import Minted from './Minted.vue'
import CountdownPublic from './CountdownPublic.vue'
import PlusMinusInput from './PlusMinusInput.vue'
import Transaction from './Transaction.vue'

const delimiter = 1e18
const unknown = 'xx'

const maxWLCount = 2
const maxSaleCount = 5

export default {
  name: 'Sale',
  components: { Minted, CountdownPublic, PlusMinusInput, Transaction },
  props: {},
  data() {
    return {
      titleWL: 'Private Sale is live!',
      titlePresale: 'Pre-Sale is live!',
      titleSale: 'PUBLIC SALE IS LIVE!',
      titleWLEnd:
        'Private sale has ended. Join the Pre-sale <a href="https://etherdaddy.io/" target="_blank">HERE</a>',
      titleSoldOut:
        'SOLD OUT! Buy your Ether Daddy on <a href="https://opensea.io/collection/therealdaddy/" target="_blank">Opensea</a>',
      titleError:
        'Sorry, a technical error occurred, <small>and we are working to fix it. <span>Please, check back soon!</span></small>',
      mintedCount: null,
      totalCount: null,
      priceSale: null,
      priceWL: null,
      soldOut: false,
      forceWLEnd: false,
      forceError: false,
      currentCount: 0,
      inWhiteListCount: 0,
      balance: 0,
      salesStatus: 0,
      message: {
        title: null,
        text: null,
        show: false
      },
      modal: {
        title: null,
        text: null,
        show: false
      },
      isMounted: false
    }
  },
  computed: {
    isMobileDevice() {
      return this.$root.isMobileDevice
    },
    isWalletConnected() {
      return this.$root.isWalletConnected
    },
    currentWallet() {
      return this.$root.currentWallet
    },
    contractEther() {
      return this.$root.contractEther
    },
    inWhitelist() {
      return this.$root.inWhitelist
    },
    isPresaleStart() {
      return this.$root.isPresaleStart
    },
    isPresaleEnd() {
      return this.$root.isPresaleEnd
    },
    isPresaleOn() {
      return this.$root.isPresaleOn
    },
    isSaleOn() {
      return this.$root.isSaleOn
    },
    forceWL() {
      return this.$root.forceWL
    },
    computedLeftCount() {
      return this.mintedCount !== null ? this.totalCount - this.mintedCount : 0
    },
    computedMintedCount() {
      return this.mintedCount !== null ? this.mintedCount : unknown
    },
    computedTotalCount() {
      return this.totalCount !== null ? this.totalCount : unknown
    },
    maxMintCount() {
      let count = maxSaleCount

      if (this.isWLSale || this.isPresaleOn) {
        count = this.balance > 0 ? maxWLCount - this.balance : maxWLCount
        count = count < 0 ? 0 : count
      }

      return this.computedLeftCount < count ? this.computedLeftCount : count
    },
    minMintCount() {
      return this.computedLeftCount === 0 || this.maxMintCount === 0 ? 0 : 1
    },
    isPaused() {
      //  
      return this.salesStatus === 0
    },
    isSale() {
      //      
      return this.salesStatus === 1
    },
    isWLSale() {
      //   
      // return this.salesStatus === 2 || this.forceWL
      return this.forceWL
    },
    price() {
      return this.isWLSale ? this.priceWL : this.priceSale
    },
    priceFormat() {
      return (this.currentCount * (this.price / delimiter)).toFixed(2)
    },
    priceText() {
      return `GET YOURS, DADDY  ${this.priceFormat}`
    },
    title() {
      if (this.isWLSale) {
        return this.titleWL
      } else if (this.isPresaleOn) {
        return this.titlePresale
      } else {
        return this.titleSale
      }
    },
    titleMax() {
      let count = maxSaleCount

      if (this.isWLSale || this.isPresaleOn) {
        count = maxWLCount
      }

      if (this.isSaleOn) {
        return `MAX ${count} NFTS PER TRANSACTION`
      } else {
        return `MAX ${count} NFTS PER ADDRESS`
      }
    },
    computedCountDown() {
      const { minutes, seconds } = this.$root.countDownSale
      return {
        minutes: this.$formatNumber(minutes),
        seconds: this.$formatNumber(seconds)
      }
    },
    titlePresaleEnd() {
      const { minutes, seconds } = this.computedCountDown
      const time = minutes.join('') + ':' + seconds.join('')

      return `Pre-sale has ended! Public sale will start in ${time}.<small>If the page wont update automatically, please refresh it manually after 4:30 PM EST</small>`
    },
    end() {
      const end = { state: false, title: null }

      if (this.forceError) {
        end.title = this.titleError
        end.state = true
      } else if (this.forceWLEnd) {
        end.title = this.titleWLEnd
        end.state = true
      } else if (this.soldOut) {
        end.title = this.titleSoldOut
        end.state = true
      } else if (this.isPresaleEnd && !this.isSaleOn) {
        end.title = this.titlePresaleEnd
        end.state = true
      } else {
        end.title = this.titleError
      }

      return end
    }
  },
  watch: {
    currentWallet(newV, oldV) {
      this.web3Handler()
    }
  },
  created() {},
  async mounted() {
    await this.web3Handler()

    setTimeout(() => {
      if (!this.isWalletConnected && !this.isMobileDevice && !this.end.state) {
        this.$root.connectMetaMask()
      }
    }, 1000)

    // this.testModal()

    this.isMounted = true
  },
  methods: {
    async web3Handler() {
      const { contractEther, currentWallet } = this

      if (!contractEther) {
        return new Promise(resolve => {
          setTimeout(async () => {
            await this.web3Handler()
            resolve()
          }, 500)
        })
      }

      let response = null

      try {
        response = await Promise.all([
          contractEther.methods.MAX_TOKENS().call(),
          contractEther.methods.totalSupply().call(),
          contractEther.methods.salesStatus().call(),
          contractEther.methods.price().call(),
          contractEther.methods.priceWL().call(),
          currentWallet !== null ? contractEther.methods.inWhiteListCount(currentWallet).call() : 0,
          currentWallet !== null ? contractEther.methods.balanceOf(currentWallet).call() : 0
        ]).catch(error => {
          console.log(error)
          setTimeout(() => {
            this.web3Handler()
          }, 1000)
        })
      } catch (error) {
        console.log(error)

        return Promise.resolve()
      }

      const [totalCount, mintedCount, salesStatus, price, priceWL, inWhiteListCount, balance] =
        response

      this.totalCount = Number(totalCount)
      this.mintedCount = Number(mintedCount)
      this.salesStatus = Number(salesStatus)
      this.priceSale = Number(price)
      this.priceWL = Number(priceWL)
      this.inWhiteListCount = Number(inWhiteListCount)
      this.balance = Number(balance)

      this.getBalanceCookie()
      this.setCurrentCount()
      this.setSoldOut()

      return Promise.resolve()
    },
    async isReadyToMint() {
      if (!this.isWalletConnected) {
        this.showMessage('Metamask is not connected.', 'Please connect it and try again.')
        this.$root.connectMetaMask()
        return Promise.resolve(false)
      }

      if (this.isWLSale) {
        if (this.inWhiteListCount === 0) {
          this.showMessage(
            'You are not eligible to mint during the private sale.',
            'Please wait for the pre-sale or public sale.'
          )
          return Promise.resolve(false)
        }

        if (this.currentCount > this.maxMintCount || this.balance >= maxWLCount) {
          this.showMessage(
            'You have minted 2 Ether Daddy NFTs already!',
            'Only 2 NFTs per address can be minted during the private sale.'
          )
          return Promise.resolve(false)
        }
      }

      if (this.isPresaleOn && !this.isWLSale) {
        if (!this.inWhitelist) {
          this.showMessage(
            'You are not eligible to mint. Only those that have traded 1.5 ETH or more on Opensea will be able to mint during the Pre-sale.',
            'Please wait for the Public sale to start at 4:30 PM EST.'
          )
          return Promise.resolve(false)
        }

        if (this.currentCount > this.maxMintCount || this.balance >= maxWLCount) {
          this.showMessage(
            'You have minted 2 Ether Daddy NFTs already!',
            'Only 2 NFTs per address can be minted during the Pre-sale.'
          )
          return Promise.resolve(false)
        }
      }

      if (this.isPresaleEnd && !this.isSaleOn) {
        this.showMessage(
          'The presale with a price of 0.1 ether is completed, in 4:30 PM EST the main public sale starts.',
          'If the page does not update itself, then do it manually after 4:30 PM EST'
        )
        return Promise.resolve(false)
      }

      if (this.isPaused) {
        this.showMessage('Smart contract paused')
        return Promise.resolve(false)
      }

      if (this.currentCount > this.maxMintCount) {
        this.showMessage(`Max tokens are ${this.maxMintCount}`)
        return Promise.resolve(false)
      }

      if (this.computedLeftCount <= 0) {
        this.showMessage('No daddies available')
        return Promise.resolve(false)
      }

      return Promise.resolve(true)
    },
    async handleMint() {
      this.FBLead()

      await this.web3Handler()

      const ready = await this.isReadyToMint()

      if (ready) {
        this.mint()
      }
    },
    mint() {
      const {
        contractEther,
        currentWallet,
        price,
        currentCount,
        web3Handler,
        FBPurchase,
        setBalanceCookie,
        showModal,
        closeModal
      } = this

      contractEther.methods
        .mint(currentWallet, currentCount)
        .send({
          from: currentWallet,
          value: currentCount * price
        })
        .on('sending', function (payload) {})
        .on('sent', function (payload) {
          showModal(`Transaction confirmation is pending`)
        })
        .on('transactionHash', hash => {
          FBPurchase()
          setBalanceCookie()
          showModal(
            `Your transaction`,
            `<a href="https://etherscan.io/tx/${hash}" target="_blank">${hash}</a><small>After TX is successfully confirmed, you can check out your ETHER DADDY NFTs on OpenSea <a href="https://opensea.io/account/therealdaddy" target="_blank">here</a></small>`
          )
          web3Handler()
        })
        .on('confirmation', confirmationNumber => {})
        .on('error', error => {
          if (error.code !== 4001) {
            this.$notify({
              group: 'app-notifications',
              type: 'error',
              text: String(error).includes('eth_sendTransaction')
                ? 'Please connect to Ethereum Mainnet'
                : error
            })
          }
          closeModal()
          web3Handler()
        })
    },
    setSoldOut() {
      if (this.mintedCount >= this.totalCount) {
        this.soldOut = true
        this.$root.soldOut = true
      }
    },
    setCurrentCount() {
      if (this.currentCount === 0) {
        this.currentCount = this.maxMintCount >= 3 ? 3 : this.maxMintCount
      }
    },
    getBalanceCookie() {
      const { currentWallet, isWLSale, isPresaleOn, balance } = this

      if (isWLSale || isPresaleOn) {
        const balanceCookie = Number(Cookies.get(`balance_wl_${currentWallet}`) || 0)
        if (balanceCookie > balance) {
          this.balance = balanceCookie
        }
      }
    },
    setBalanceCookie() {
      const { currentWallet, currentCount, isWLSale, isPresaleOn, balance } = this

      if (isWLSale || isPresaleOn) {
        const balanceCookie = balance + Number(currentCount)
        const minute = new Date(new Date().getTime() + 1 * 60 * 1000)
        Cookies.set(`balance_wl_${currentWallet}`, balanceCookie, {
          expires: minute // 1/1440
        })
      }
    },
    showModal(title = '', text = '') {
      this.modal.title = title
      this.modal.text = text
      this.modal.show = true
    },
    closeModal() {
      this.modal.show = false
      this.modal.title = null
      this.modal.text = null
    },
    showMessage(title = '', text = '', hide = true) {
      this.message.title = title
      this.message.text = text
      this.message.show = true

      if (hide) {
        setTimeout(() => {
          this.message.show = false
        }, 10000)
      }
    },
    FBLead() {
      try {
        /* eslint-disable-next-line */
        FbEvents.Lead(
          [
            {
              id: this.mintedCount,
              quantity: this.currentCount,
              item_price: (this.price / delimiter).toFixed(2)
            }
          ],
          this.currentWallet
        )
      } catch (error) {
        console.log(error)
      }
    },
    FBPurchase() {
      try {
        /* eslint-disable-next-line */
        FbEvents.Purchase(
          [
            {
              id: this.mintedCount,
              quantity: this.currentCount,
              item_price: (this.price / delimiter).toFixed(2)
            }
          ],
          this.currentWallet
        )
      } catch (error) {
        console.log(error)
      }
    },
    testModal() {
      this.showModal(
        `Your transaction`,
        `<a href="https://etherscan.io/tx/sadjkh08098410921jkfhkjhfsdsd7688" target="_blank">sadjkh08098410921jkfhkjhfsdsd7688</a><small>After TX is successfully confirmed, you can check out your ETHER DADDY NFTs on OpenSea <a href="https://opensea.io/account/therealdaddy" target="_blank">here</a></small>`
      )
    }
  }
}
</script>
